
 table {
	 display: block;
}
 tr, td, tbody, tfoot {
	 display: block;
}
 thead {
	 display: none;
}
 tr {
	 padding-bottom: 10px;
}
 td {
	 padding: 10px 10px 0;
	 text-align: center;
}
 td:before {
	 content: attr(data-title);
	 color: #7a91aa;
	 text-transform: uppercase;
	 font-size: 1.4rem;
	 padding-right: 10px;
	 display: block;
}
 table {
	 width: 100%;
}
 th {
	 text-align: left;
	 font-weight: 700;
}
 thead th {
	 background-color: #202932;
	 color: #fff;
	 border: 1px solid #202932;
}
 tfoot th {
	 display: block;
	 padding: 10px;
	 text-align: center;
	 color: #b8c4d2;
}
 .button {
	 line-height: 1;
	 display: inline-block;
	 font-size: 1.2rem;
	 text-decoration: none;
	 border-radius: 5px;
	 color: #fff;
	 padding: 8px;
	 background-color: #4b908f;
}
 @media (min-width: 460px) {
	 td {
		 text-align: left;
	}
	 td:before {
		 display: inline-block;
		 text-align: right;
		 width: 140px;
	}
	 .select {
		 padding-left: 160px;
	}
}
 @media (min-width: 720px) {
	 table {
		 display: table;
	}
	 tr {
		 display: table-row;
	}
	 td, th {
		 display: table-cell;
	}
	 tbody {
		 display: table-row-group;
	}
	 thead {
		 display: table-header-group;
	}
	 tfoot {
		 display: table-footer-group;
	}
	 td {
		 border: 1px solid #28333f;
	}
	 td:before {
		 display: none;
	}
	 td, th {
		 padding: 10px;
	}
	 tr:nth-child(2n+2) td {
		 background-color: #242e39;
	}
	 tfoot th {
		 display: table-cell;
	}
	 .select {
		 padding: 10px;
	}
}
 